.rc-tree-select-tree li span.rc-tree-select-tree-iconEle {
	line-height: 16px;
	margin-right: 2px;
	width: 16px;
	height: 16px;
	display: inline-block;
	vertical-align: middle;
	border: 0 none;
	cursor: pointer;
	outline: none;
	background-color: transparent;
	background-repeat: no-repeat;
	background-attachment: scroll;
	background-image: url('data:image/png;base64,');
}
.rc-tree-select-tree li span.rc-tree-select-tree-icon_loading {
	margin-right: 2px;
	vertical-align: top;
	background: url('data:image/gif;base64,')
		no-repeat scroll 0 0 transparent;
}
.rc-tree-select-tree li span.rc-tree-select-tree-switcher-noop {
	cursor: auto;
	background: none;
}
.rc-tree-select-tree li span.rc-tree-select-tree-switcher_open {
	background-position: -93px -56px;
}
.rc-tree-select-tree li span.rc-tree-select-tree-switcher_close {
	background-position: -75px -56px;
}
.rc-tree-select-tree li span.rc-tree-select-tree-checkbox {
	width: 13px;
	height: 13px;
	margin: 0 3px;
	background-position: 0 0;
}
.rc-tree-select-tree li span.rc-tree-select-tree-checkbox-checked {
	background-position: -14px 0;
}
.rc-tree-select-tree li span.rc-tree-select-tree-checkbox-indeterminate {
	background-position: -14px -28px;
}
.rc-tree-select-tree li span.rc-tree-select-tree-checkbox-disabled {
	background-position: 0 -56px;
}
.rc-tree-select-tree
	li
	span.rc-tree-select-tree-checkbox.rc-tree-select-tree-checkbox-checked.rc-tree-select-tree-checkbox-disabled {
	background-position: -14px -56px;
}
.rc-tree-select-tree
	li
	span.rc-tree-select-tree-checkbox.rc-tree-select-tree-checkbox-indeterminate.rc-tree-select-tree-checkbox-disabled {
	position: relative;
	background: #ccc;
	border-radius: 3px;
}
.rc-tree-select-tree
	li
	span.rc-tree-select-tree-checkbox.rc-tree-select-tree-checkbox-indeterminate.rc-tree-select-tree-checkbox-disabled::after {
	content: ' ';
	-webkit-transform: scale(1);
	transform: scale(1);
	position: absolute;
	left: 3px;
	top: 5px;
	width: 5px;
	height: 0;
	border: 2px solid #fff;
	border-top: 0;
	border-left: 0;
}
.rc-tree-select-tree-child-tree {
	display: none;
}
.rc-tree-select-tree-child-tree-open {
	display: block;
}
.rc-tree-select-tree-treenode-disabled > span,
.rc-tree-select-tree-treenode-disabled > a,
.rc-tree-select-tree-treenode-disabled > a span {
	color: #ccc;
	cursor: not-allowed;
}
.rc-tree-select-tree-node-selected {
	background-color: #ffe6b0;
	border: 1px #ffb951 solid;
	opacity: 0.8;
}
.rc-tree-select-tree-icon__open {
	margin-right: 2px;
	background-position: -110px -16px;
	vertical-align: top;
}
.rc-tree-select-tree-icon__close {
	margin-right: 2px;
	background-position: -110px 0;
	vertical-align: top;
}
.tree-select-dark-mode .rc-tree-select-search__field__placeholder i {
	color: #ffffff !important;
}
