.chat-bubble__selected-emojies {
  //position: absolute;
  max-width: 100%;
  margin: -10px;
  padding: 10px;
  border-radius: 40px;
  .PriorityNav_Root {
    //width: 100%;
    //max-width: 400px;
    max-width: 100%;

    @include respond(phone) {
      max-width: 180px;
    }
  }
  .overflow-btn {
    position: absolute;

    top: 10px;
    z-index: 1;
    width: 42px;
    height: 33px;
    padding: 0;
    background-color: #fff;
    box-shadow: 1px 1px 3px rgba(#000, .2);
    cursor: pointer;
    border-radius: 15px;
    transition: all .3s;
    &:hover {
      background-color: #fff;
      box-shadow: none;
      //transform: translateY(-3px);
    }
  }
}

.chat-bubble__selected-emojies .overflow-btn {
  left:-1px;
  .even & {
    right: -1px;
    left: auto;
  }


}

.chat-emoji {
  margin-right: 5px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 15px;
  background-color: #fff;
  box-shadow: 1px 1px 3px rgba(#000, 0.2);
  padding: 5px;
  cursor: pointer;
  transition: all 0.3s;

  &:not(:first-child) {
    margin-left: 5px;
  }
  &:first-child {
    margin-left: -10px;
  }

  &:hover {
    box-shadow: none;
    transform: translateY(-3px);
  }
}

.chat-emoji__count {
  margin-left: 5px;
  font-size: 13px;
  font-weight: bold;
  color: #000;
}

.chat-emoji__unicode {
  line-height: 1;
}

/* emoji mart*/
.emoji-mart {
  //margin-top: 20px;
}

.chat-bubble__react-emoji-more {
  .MuiIconButton-root {
    padding-top: 8px;
    padding-bottom: 8px;
  }
}
.chat-bubble__react-emoji-custom-dark {
  .emoji-mart-scroll .emoji-mart-category {
    ul li button::before {
      background-color: #2b2b2b !important;
    }
  }
}

.chat-bubble__react-emoji-custom {
  position: relative;
  z-index: 1;
  left: 5px;
  top: 3px;
  .emoji-mart {
    background-color: transparent !important;
    border-color: transparent !important;
  }
  .emoji-mart-scroll  {
    padding: 0;
  }



  & > .emoji-mart {
    width: 255px !important;
    //z-index: 999;
  }
  .emoji-mart .emoji-mart-anchors {
    display: none;
  }
  .emoji-mart .emoji-mart-search {
    display: none;
  }
  .emoji-mart .emoji-mart-bar {
    display: none;
  }
  .emoji-mart .emoji-mart-scroll {
    overflow: auto;
    height: auto;
  }
  .emoji-mart .emoji-mart-category-label {
    display: none;
  }
}

.odd .chat-bubble__react-emoji-wrp {
  right: 0;
}



