//countable-emojies
.countable-emojies {
  .PriorityNav_Main {
    //display: flex;

    div {
      vertical-align: middle;
    }
  }

  .PriorityNav_Button {
    height: 29px;

    background-color: #fff;
    border-radius: 15px;
    box-shadow: 2px 2px 5px rgba(#000, 0.2);
    padding: 5px 15px;
    svg path {
      fill: #999;
    }
  }
  .countable-emojies__more-list {
    right: 0;
  }
}

.countable-emojies_mine {
  .PriorityNav_Button {
    float: left;
  }

  .countable-emojies__more-list {
    left: 0;
    right: auto;
  }
}

.countable-emojies__more-list {
  position: absolute;
  top: calc(100% + 5px);
  z-index: 12;
  background-color: #fff;
  border-radius: 5px;

  .chat-emoji {
    margin: 0;
    box-shadow: none;
    background-color: transparent;
  }
  .chat-emoji:not(:last-child) {
    //margin-bottom: 5px;
  }
}

