.file-msg-like {
  position: absolute;
  bottom: 10px;
  right: 10px;
  z-index: 1;
  cursor: pointer;
  transition: all .3s;

  &:hover {
    transform: scale(1.1);
  }

  &_location {
    bottom: 40px;
    z-index: 1;
  }

  &_audio {
    bottom: 25px;
    right: 13px;
  }

  &_file {
    bottom: 20px
  }

  &_video {
    bottom: -27px;
  }
  &_video_edit {
    bottom:3px;
    right: 26px;
  }
}

.chat-bubble-emoji-count-row {
  position: absolute;
  top: calc(100% + 5px);
  .chat-bubble__selected-emojies {
    margin-left: 0!important;
  }

  right: 0
}

.even .chat-bubble-emoji-count-row {
  left: 0;
  right: auto;
}

.has-reactions {
  margin-bottom: 35px;
}

