    //text-align: right;
    //font-weight: bold;
    //font-size: 20px;
    //line-height: 1.2;
    position: absolute;
    height: 33px;
    //height: 42px;
    width: 36px;
    top: 10px;
    //top: 6px;
    left: 0;
    pointer-events: none;
    //background: linear-gradient(to right, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 32%,rgba(255,255,255,1) 100%);
    background: #f8f9fa;

  }

  .even & {
    &::after {
      right: 0;
      left: auto;
    }
  }
}

