	/* switch-light
*/
	.switch-light.switch-material a {
		top: -3px;
		width: 28px;
		height: 28px;
		border-radius: 50%;
		background: #fafafa;
		box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 2px -2px rgba(0, 0, 0, 0.2),
