	/* switch-toggle-custom
*/
	.switch-toggle-custom.switch-material {
		overflow: visible;
	}
	.switch-toggle-custom.switch-material::after {
		clear: both;
		content: '';
		display: table;
	}
	.switch-toggle-custom.switch-material a {
		top: 48%;
		width: 6px !important;
		height: 6px;
		margin-left: 4px;
		background: #3f51b5;
		border-radius: 100%;
		transform: translateY(-50%);
		transition: transform 0.4s ease-in;
	}
	.switch-toggle-custom.switch-material label {
		color: rgba(0, 0, 0, 0.54);
		font-size: 1em;
	}
	.switch-toggle-custom.switch-material label:before {
		content: '';
		position: absolute;
		top: 48%;
		left: 0;
		display: block;
		width: 14px;
		height: 14px;
		border-radius: 100%;
		border: 2px solid rgba(0, 0, 0, 0.54);
		transform: translateY(-50%);
	}
	.switch-toggle-custom.switch-material input:checked + label:before {
		border-color: #3f51b5;
	}
	/* ripple
*/
	.switch-light.switch-material > span:before,
	.switch-light.switch-material > span:after,
	.switch-toggle-custom.switch-material label:after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: 3;
		display: block;
		width: 64px;
		height: 64px;
		border-radius: 100%;
		background: #3f51b5;
		opacity: 0.4;
		margin-left: -20px;
		margin-top: -20px;
		transform: scale(0);
		transition: opacity 0.4s ease-in;
	}
	.switch-light.switch-material > span:after {
		left: auto;
		right: 0;
		margin-left: 0;
		margin-right: -20px;
	}
	.switch-toggle-custom.switch-material label:after {
		width: 52px;
		height: 52px;
		margin-top: -12px;
	}
	@keyframes materialRipple {
		0% {
			transform: scale(0);
		}
		20% {
			transform: scale(1);
		}
		100% {
			opacity: 0;
			transform: scale(1);
		}
	}
	.switch-material.switch-light input:not(:checked) ~ span:after,
	.switch-material.switch-light input:checked ~ span:before,
	.switch-toggle-custom.switch-material input:checked + label:after {
		animation: materialRipple 0.4s ease-in;
	}
	/* trick to prevent the default checked ripple animation from showing
* when the page loads.
* the ripples are hidden by default, and shown only when the input is focused.
*/
	.switch-light.switch-material.switch-light input ~ span:before,
	.switch-light.switch-material.switch-light input ~ span:after,
	.switch-material.switch-toggle-custom input + label:after {
		visibility: hidden;
	}
	.switch-light.switch-material.switch-light input:focus:checked ~ span:before,
	.switch-light.switch-material.switch-light input:focus:not(:checked) ~ span:after,
	.switch-material.switch-toggle-custom input:focus:checked + label:after {
		visibility: visible;
	}
}

