//Right to left layout styles
html[dir="rtl"] {
  .rct-scroll > div {
	overflow: -moz-scrollbars-none;
	-ms-overflow-style: none;
	scrollbar-width: none;
  }

  .rct-scroll > div::-webkit-scrollbar {
	display: none;
  }

  .react-tel-input .selected-flag {
	.arrow {
	  left: 8px;
	}

	.flag {
	  left: 25%;
	}
  }
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background: rgba(211, 211, 211, 0.1);
}

::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	border-radius: 10px;
	visibility: hidden;
}

::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background-color: rgba(0, 0, 0, 0.2);
	// -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
}

.app-mode__dark {
	::-webkit-scrollbar-thumb {
		background-color: rgba(255, 255, 255, 0.3);
		// -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
	}
}

html {
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.slide-pane__overlay.overlay-after-open {
	z-index: 1400;
}



mark.highlighted {
	background-color: transparent;
	color: red;
}

.filter-date-range-picker {
	width: 100%;

	.rdrMonths,
	.rdrMonth {
		width: 100% !important;
	}

	.rdrDayToday .rdrDayNumber span::after {
		background-color: #00d0bd;
	}

	::-webkit-scrollbar {
		width: 2px;
	}

	::-webkit-scrollbar-track {
		background: #f1f1f1;
	}

	::-webkit-scrollbar-thumb {
		background: #888;
	}

	::-webkit-scrollbar-thumb:hover {
		background: #555;
	}
}

.filter-date-range-picker__dark {
	.rdrDateDisplayWrapper {
		background-color: #2b2b2b;

		.rdrDateDisplayItem {
			background: inherit;
		}
	}

	.rdrDayNumber span {
		color: #fff;
	}

	.rdrMonthAndYearWrapper {
		.rdrNextPrevButton {
			background: inherit;
		}

		.rdrNextButton i {
			border-color: transparent transparent transparent #fff;
		}

		.rdrPprevButton i {
			border-color: transparent #fff transparent transparent;
		}

		.rdrMonthAndYearPickers select {
			color: #fff;
			background: #2d2d2d;
		}
	}
}

.phone-input-dark {
	.form-control {
		background-color: #1e1e1e !important;
		border-color: rgba(255, 255, 255, 0.3) !important;
	}

	.selected-flag, .country-list, .country-list .search, .country-list .search .search-box {
		background-color: #1e1e1e !important;
		border-color: rgba(255, 255, 255, 0.3) !important;
	}

	.country-list .country.highlight {
		background: rgba(255, 255, 255, 0.1) !important;
	}

	.country-list .country:hover > .country-name {
		color: grey;
	}

	.country-list .country-name {
		color: #fff !important
	}
}

.react-select-paginate {
	[class*='-indicatorContainer'] {
		width: 40px;
		height: 40px;
		justify-content: center;
		align-items: center;
	}

	[class*='-menu'] {
		z-index: 1000;
	}
}

.dark {
	.react-select-dropdown {
		[class*='css-'] {
			[class*='option']:hover, 
			[class*='is-selected'] {
				background-color: rgba(0, 208, 189, .2);
			}

			[class*='multiValue'], 
			[class*='multiValue'] div {
				background-color: #888;
				border-radius: 0;				
			}

			[class*='multiValue'] div:last-child:hover, 
			[class*='multiValue'] div:last-child:hover > svg  {
				background-color: #FFBDAD;
    			color: #DE350B;
			}
			background-color: #1e1e1e;
			color: #fff;
			border-radius: 10px;
		}
		[class*='-container'] {
			border-radius: 10px;
		}
	}

	/* WebKit, Edge */
	::-webkit-input-placeholder {
		color: #fff;
	}
	/* Firefox 4-18 */
	:-moz-placeholder {
		color: #fff;
		opacity: 1;
	}
	/* Firefox 19+ */
	::-moz-placeholder {
		color: #fff;
		opacity: 1;
	}
	/* IE 10-11 */
	:-ms-input-placeholder {
		color: #fff;
	}
	/* Edge */
	::-ms-input-placeholder {
		color: #fff;
	}
	/* MODERN BROWSER */
	::placeholder {
		color: #fff;
	}

	//.rdrCalendarWrapper {
	//    color: white!important;
