}

.chat-dropzone {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	display: none;
	background-color: #fff;
	font-size: 20px;
	z-index: 10011;
	.reject &,
	.accept & {
		display: flex;
		align-items: center;
		justify-content: center;
		z-index: 2;
	}
}

.chat.accept {
	.message-like-button,
	.pinned-case {
		z-index: 0 !important;
	}
}

.chat-panel__resize-handler {
	cursor: col-resize;
}
