.lb-custom-wrapper .lb-icon-arrow {
	display: none;
}

.lb-icon-arrow {
	position: absolute;
	top: 50%;
	padding: 12px 12px !important;
	background-size: 100% !important;
	background-image: url("data:image/png;base64,") !important;
}


.lb-icon-arrow.prev {
	left: 5%;
	transform: scale(2);

}

.lb-icon-arrow.next {
	right: 5%;
	transform: rotateY(180deg) scale(2) !important;
}

#notistack-snackbar {
